html {
  font-size: 62.5%;
}

.contents-area {
  width: 100%;
  border-top: 1px black solid;
}

.contents-area:nth-of-type(4) {
  border-bottom: 1px black solid;
}

.contents-item {
  padding: 20px 0;
  display: flex;
  width: 100%;
}

.contents-img {
  width: 25%;
}

.contents-img img {
  width: 100%;
}

.contents-textarea {
  width: 75%;
  padding-left: 3%;
}

.contents-heading {
  font-size: 2.4rem;
}

.contents-text {
  font-size: 1.6rem;
}

.pagevisual {
  height: 250px;
  width: 100%;
  background: url(../../../img/mv.png) center/cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagevisual-ttl {
  color: white;
}
