.contents{
  
 &-area{
   width: 100%;
   border-top: 1px black solid;
  &:nth-of-type(4){
    border-bottom: 1px black solid;
  }
 }
 &-item{
   padding: 20px 0;
   display: flex;
   width: 100%;

 }
 &-img{
   width: 25%;
   img{
     width: 100%;
   }
 }
 &-textarea{
   width: 75%;
   padding-left: 3%;
 }
 &-heading{
   @include fz(24);
 }
 &-text{
   @include fz(16);
 }
}